	background-image: url(https://opengear.com/wp-content/uploads/2018/05/StarfieldSimulation.gif);
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

