/* * Go to top button */
.top-to-btm {
	position: relative;
}
.icon-position {
	position: fixed;
	bottom: 40px;
	right: 25px;
	z-index: 20;
}
.icon-style {
	background-color: #209db6;
	border: 2px solid rgb(14, 181, 131);
	border-radius: 50%;
	height: 50px;
	width: 50px;
	color: azure;
	cursor: pointer;
	animation: movebtn 3s ease-in-out infinite;
	transition: all 0.5s ease-in-out;
}
.icon-style:hover {
	animation: none;
	background-color: RGB(42, 176, 238);
	box-shadow: 0 0 50px RGB(42, 176, 238);
}
@keyframes movebtn {
	0% {
		transform: translateY(0px);
	}
	25% {
		transform: translateY(20px);
	}
	50% {
		transform: translateY(0px);
	}
	75% {
		transform: translateY(-20px);
	}
	100% {
		transform: translateY(0px);
	}
}
